/**
 * Retrieves the translation of text.
 *
 * @see https://developer.wordpress.org/block-editor/reference-guides/packages/packages-i18n/
 */
import { __ } from '@wordpress/i18n';

/**
 * React hook that is used to mark the block wrapper element.
 * It provides all the necessary props like the class name.
 *
 * @see https://developer.wordpress.org/block-editor/reference-guides/packages/packages-block-editor/#useblockprops
 */
import { useBlockProps } from '@wordpress/block-editor';

/**
 * Lets webpack process CSS, SASS or SCSS files referenced in JavaScript files.
 * Those files can contain any CSS code that gets applied to the editor.
 *
 * @see https://www.npmjs.com/package/@wordpress/scripts#using-css
 */
import './editor.scss';

/**
 * The edit function describes the structure of your block in the context of the
 * editor. This represents what the editor will render when the block is used.
 *
 * @see https://developer.wordpress.org/block-editor/reference-guides/block-api/block-edit-save/#edit
 *
 * @return {WPElement} Element to render.
 */

import { Fragment } from '@wordpress/element';
import { InspectorControls, BlockControls, AlignmentControl, PanelColorSettings } from '@wordpress/block-editor';
import { RangeControl, ToggleControl, PanelBody } from '@wordpress/components';
import ServerSideRender from '@wordpress/server-side-render';

export default function Edit( props ) {
	const { setAttributes, attributes } = props;
	const { textAlign, showFromLabel, hideRegularPrice, color, fontSize } = attributes;
	
	const colorSettings = [
		{
			value: color,
			onChange: colorValue => setAttributes({ color: colorValue }),
			label: __( "Color", 'wp-travel-blocks' )
		}
	];
	return (
		<div { ...useBlockProps() }>
			<Fragment>
				<BlockControls>
					<AlignmentControl
						value={textAlign}
						onChange={(nextAlign) => {
							setAttributes({ textAlign: nextAlign });
						}}
					/>
				</BlockControls>
				<InspectorControls>
					<PanelBody title={ __( 'Trip Price Options', 'wp-travel-blocks' ) } >
						<ToggleControl
							label={ __( 'Show From' ) }
							checked={ showFromLabel }
							onChange={ () => setAttributes( { showFromLabel: ! showFromLabel } ) }
						/>
						<ToggleControl
							label={ __( 'Hide Regular Price', 'wp-travel-blocks' ) }
							help={ __( 'This will hide regular price in case of enabled sale price.', 'wp-travel-blocks' ) }
							checked={ hideRegularPrice }
							onChange={ () => setAttributes( { hideRegularPrice: ! hideRegularPrice } ) }
						/>
						<RangeControl
							label={__('Font Size', 'wp-travel-blocks')}
							onChange={ (value) => setAttributes( { fontSize: value } ) }
							shiftStep={ 1 }
							min={ 2 }
							max={ 100 }
							value={ fontSize }
						/>
					</PanelBody>
					<PanelColorSettings
						title={__('Colors', 'wp-travel-blocks')}
						colorSettings={colorSettings}
						initialOpen={false}
					/>
				</InspectorControls>
			</Fragment>
			<ServerSideRender
				block="wptravel/trip-price"
				attributes={ props.attributes }
			/>
		</div>
	);
}
